.styles {
  margin-bottom: 40px;

  &:nth-child(3n+4) {
    margin-left: 0;
  }

  .product-details {
    float: none;
    width: 100%;
  }

}

.style-picker {
  margin-bottom: 20px;

  div {
    display: inline-block;
    width: 40px;
    height: 25px;
    margin: 0 5px 0 0;
    border: 0;
    cursor: pointer;
    -webkit-appearance: inherit;
  }

}

.display-none {
  display: none;
}

.value-button {
  display: inline-block;
  border: 1px solid #ddd;
  margin-bottom: 2px;
  width: 40px;
  height: 40px;
  text-align: center;
  vertical-align: middle;
  padding: 9px 0;
  background: #eee;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.value-button:hover {
  cursor: pointer;
}

#decrease {
  margin-right: -4px;
  border-radius: 4px 0 0 4px;
  // height: 40px;

}

#increase {
  margin-left: -4px;
  border-radius: 0 4px 4px 0;
  // height: 40px;

}
.plus {
  margin-left: -4px;
  border-radius: 0 4px 4px 0;
}
.minus {
  margin-right: -4px;
  border-radius: 4px 0 0 4px;
}

